/**
 * AgriAI Chat API Client
 * 
 * Frontend client per l'integrazione con il backend chat system
 * Fornisce:
 * - REST API calls per chat
 * - WebSocket integration
 * - Error handling robusto
 * - TypeScript type safety
 */

import { authApi, ApiResponse } from './authApi';

// ================================
// Types & Interfaces
// ================================

export interface ChatMessage {
  id: string;
  content: string;
  sender: 'user' | 'ai';
  timestamp: Date;
  conversationId?: string;
  confidence?: number;
  sources?: string[];
  metadata?: Record<string, any>;
}

export interface Conversation {
  id: string;
  title?: string;
  userId: string;
  status: 'active' | 'completed' | 'archived';
  createdAt: Date;
  updatedAt: Date;
  messageCount: number;
  lastMessage?: ChatMessage;
}

export interface ChatContext {
  userType: 'public' | 'member';
  location?: string;
  preferences?: Record<string, any>;
}

export interface SendMessageRequest {
  content: string;
  conversationId?: string;
  context?: ChatContext;
}

export interface SendMessageResponse {
  userMessage: ChatMessage;
  aiMessage: ChatMessage;
  conversation: Conversation;
  processingTime: number;
}

export interface ConversationsResponse {
  conversations: Conversation[];
  total: number;
  hasMore: boolean;
}

export interface FeedbackRequest {
  messageId: string;
  rating: 'positive' | 'negative';
  comment?: string;
}

// ================================
// API Configuration
// ================================

const CHAT_ENDPOINTS = {
  SEND_MESSAGE: '/api/chat/query',
  GET_CONVERSATIONS: '/api/chat/conversations',
  SUBMIT_FEEDBACK: '/api/chat/feedback',
  GET_DOCUMENT_STATS: '/api/chat/document-usage-stats'
} as const;

// ================================
// Chat API Client Class
// ================================

class ChatApiClient {
  /**
   * Invia un messaggio e riceve risposta AI
   */
  async sendMessage(request: SendMessageRequest): Promise<SendMessageResponse> {
    try {
      // Make direct fetch request to avoid authApi wrapper
      const url = `${import.meta.env.VITE_API_URL || 'http://localhost:3000'}${CHAT_ENDPOINTS.SEND_MESSAGE}`;
      const accessToken = authApi.getAccessToken();
      
      const response = await fetch(url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          ...(accessToken && { 'Authorization': `Bearer ${accessToken}` })
        },
        body: JSON.stringify(request)
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();

      // Convert timestamp strings to Date objects
      return {
        ...data,
        userMessage: {
          ...data.userMessage,
          timestamp: new Date(data.userMessage.timestamp)
        },
        aiMessage: {
          ...data.aiMessage,
          timestamp: new Date(data.aiMessage.timestamp)
        },
        conversation: {
          ...data.conversation,
          createdAt: data.conversation.createdAt ? new Date(data.conversation.createdAt) : new Date(),
          updatedAt: data.conversation.updatedAt ? new Date(data.conversation.updatedAt) : new Date()
        }
      };

    } catch (error) {
      console.error('Failed to send message:', error);
      throw error;
    }
  }

  /**
   * Recupera la lista delle conversazioni dell'utente
   */
  async getConversations(params?: {
    limit?: number;
    offset?: number;
    status?: 'active' | 'completed' | 'archived';
  }): Promise<ConversationsResponse> {
    try {
      const queryParams = new URLSearchParams();
      if (params?.limit) queryParams.append('limit', params.limit.toString());
      if (params?.offset) queryParams.append('offset', params.offset.toString());
      if (params?.status) queryParams.append('status', params.status);

      const url = params && Object.keys(params).length > 0 
        ? `${CHAT_ENDPOINTS.GET_CONVERSATIONS}?${queryParams.toString()}`
        : CHAT_ENDPOINTS.GET_CONVERSATIONS;

      const response = await authApi.request<ConversationsResponse>('GET', url);

      // Convert timestamp strings to Date objects
      return {
        ...response,
        conversations: response.conversations.map(conv => ({
          ...conv,
          createdAt: new Date(conv.createdAt),
          updatedAt: new Date(conv.updatedAt),
          lastMessage: conv.lastMessage ? {
            ...conv.lastMessage,
            timestamp: new Date(conv.lastMessage.timestamp)
          } : undefined
        }))
      };

    } catch (error) {
      console.error('Failed to get conversations:', error);
      throw error;
    }
  }

  /**
   * Invia feedback per un messaggio AI
   */
  async submitFeedback(request: FeedbackRequest): Promise<{ success: boolean }> {
    try {
      return await authApi.request<{ success: boolean }>('POST', CHAT_ENDPOINTS.SUBMIT_FEEDBACK, {
        body: request
      });
    } catch (error) {
      console.error('Failed to submit feedback:', error);
      throw error;
    }
  }

  /**
   * Ottiene statistiche sull'utilizzo dei documenti nelle chat
   */
  async getDocumentUsageStats(): Promise<{
    totalQueries: number;
    documentsUsed: number;
    averageConfidence: number;
    topSources: Array<{ source: string; usage: number }>;
  }> {
    try {
      return await authApi.request('GET', CHAT_ENDPOINTS.GET_DOCUMENT_STATS);
    } catch (error) {
      console.error('Failed to get document usage stats:', error);
      throw error;
    }
  }
}

// ================================
// WebSocket Integration Types
// ================================

export interface WebSocketMessage {
  type: 'chat_message' | 'typing_start' | 'typing_stop' | 'user_presence' | 'error';
  data?: any;
  content?: string;
  conversationId?: string;
  userId?: string;
  timestamp?: string;
}

export interface WebSocketConfig {
  url?: string;
  autoReconnect?: boolean;
  reconnectInterval?: number;
  maxReconnectAttempts?: number;
}

// ================================
// WebSocket Client Class
// ================================

export class ChatWebSocketClient {
  private ws: WebSocket | null = null;
  private config: Required<WebSocketConfig>;
  private reconnectAttempts = 0;
  private isConnecting = false;
  private messageHandlers: ((message: WebSocketMessage) => void)[] = [];
  private connectionHandlers: ((connected: boolean) => void)[] = [];

  constructor(config: WebSocketConfig = {}) {
    this.config = {
      url: config.url || this.getWebSocketUrl(),
      autoReconnect: config.autoReconnect ?? true,
      reconnectInterval: config.reconnectInterval ?? 3000,
      maxReconnectAttempts: config.maxReconnectAttempts ?? 5
    };
  }

  private getWebSocketUrl(): string {
    const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
    const host = window.location.host;
    return `${protocol}//${host}/ws/chat`;
  }

  async connect(): Promise<void> {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      return; // Already connected
    }

    if (this.isConnecting) {
      return; // Connection in progress
    }

    this.isConnecting = true;

    try {
      const token = authApi.getAccessToken();
      if (!token) {
        throw new Error('No authentication token available');
      }

      const wsUrl = `${this.config.url}?token=${token}`;
      this.ws = new WebSocket(wsUrl);

      return new Promise((resolve, reject) => {
        if (!this.ws) return reject(new Error('WebSocket creation failed'));

        this.ws.onopen = () => {
          console.log('✅ WebSocket connected to AgriAI Chat');
          this.isConnecting = false;
          this.reconnectAttempts = 0;
          this.notifyConnectionHandlers(true);
          resolve();
        };

        this.ws.onmessage = (event) => {
          try {
            const message: WebSocketMessage = JSON.parse(event.data);
            this.messageHandlers.forEach(handler => handler(message));
          } catch (error) {
            console.error('Failed to parse WebSocket message:', error);
          }
        };

        this.ws.onclose = (event) => {
          console.log('WebSocket disconnected:', event.code, event.reason);
          this.isConnecting = false;
          this.notifyConnectionHandlers(false);
          
          if (this.config.autoReconnect && this.reconnectAttempts < this.config.maxReconnectAttempts) {
            this.scheduleReconnect();
          }
        };

        this.ws.onerror = (error) => {
          console.error('WebSocket error:', error);
          this.isConnecting = false;
          reject(error);
        };
      });

    } catch (error) {
      this.isConnecting = false;
      throw error;
    }
  }

  private scheduleReconnect(): void {
    this.reconnectAttempts++;
    console.log(`Attempting to reconnect WebSocket (${this.reconnectAttempts}/${this.config.maxReconnectAttempts})...`);
    
    setTimeout(() => {
      this.connect().catch(error => {
        console.error('WebSocket reconnection failed:', error);
      });
    }, this.config.reconnectInterval);
  }

  sendMessage(message: WebSocketMessage): void {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify(message));
    } else {
      console.warn('WebSocket not connected, message not sent:', message);
    }
  }

  onMessage(handler: (message: WebSocketMessage) => void): () => void {
    this.messageHandlers.push(handler);
    
    // Return unsubscribe function
    return () => {
      const index = this.messageHandlers.indexOf(handler);
      if (index > -1) {
        this.messageHandlers.splice(index, 1);
      }
    };
  }

  onConnectionChange(handler: (connected: boolean) => void): () => void {
    this.connectionHandlers.push(handler);
    
    // Return unsubscribe function
    return () => {
      const index = this.connectionHandlers.indexOf(handler);
      if (index > -1) {
        this.connectionHandlers.splice(index, 1);
      }
    };
  }

  private notifyConnectionHandlers(connected: boolean): void {
    this.connectionHandlers.forEach(handler => handler(connected));
  }

  disconnect(): void {
    this.config.autoReconnect = false;
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
  }

  isConnected(): boolean {
    return this.ws?.readyState === WebSocket.OPEN;
  }
}

// ================================
// Singleton Instances
// ================================

export const chatApi = new ChatApiClient();
export const chatWebSocket = new ChatWebSocketClient();

// ================================
// Utility Functions
// ================================

/**
 * Helper per creare un nuovo messaggio utente
 */
export function createUserMessage(content: string, conversationId?: string): ChatMessage {
  return {
    id: `user-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
    content: content.trim(),
    sender: 'user',
    timestamp: new Date(),
    conversationId
  };
}

/**
 * Helper per validare il contenuto del messaggio
 */
export function validateMessageContent(content: string): { isValid: boolean; error?: string } {
  const trimmed = content.trim();
  
  if (!trimmed) {
    return { isValid: false, error: 'Il messaggio non può essere vuoto' };
  }
  
  if (trimmed.length > 1000) {
    return { isValid: false, error: 'Il messaggio è troppo lungo (massimo 1000 caratteri)' };
  }
  
  return { isValid: true };
}

/**
 * Helper per formattare la data dei messaggi
 */
export function formatMessageTime(timestamp: Date): string {
  const now = new Date();
  const diffMs = now.getTime() - timestamp.getTime();
  const diffMinutes = Math.floor(diffMs / (1000 * 60));
  const diffHours = Math.floor(diffMinutes / 60);
  const diffDays = Math.floor(diffHours / 24);

  if (diffMinutes < 1) {
    return 'Ora';
  } else if (diffMinutes < 60) {
    return `${diffMinutes}m fa`;
  } else if (diffHours < 24) {
    return `${diffHours}h fa`;
  } else if (diffDays < 7) {
    return `${diffDays}g fa`;
  } else {
    return timestamp.toLocaleDateString('it-IT', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
  }
}

export default chatApi;