import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

// Vector similarity calculations
export function cosineDistance(a: number[], b: number[]): number {
  if (a.length !== b.length) {
    throw new Error('Vectors must have the same length');
  }

  let dotProduct = 0;
  let normA = 0;
  let normB = 0;

  for (let i = 0; i < a.length; i++) {
    dotProduct += a[i] * b[i];
    normA += a[i] * a[i];
    normB += b[i] * b[i];
  }

  normA = Math.sqrt(normA);
  normB = Math.sqrt(normB);

  if (normA === 0 || normB === 0) {
    return 0;
  }

  const similarity = dotProduct / (normA * normB);
  return 1 - similarity; // Convert similarity to distance
}

export function cosineSimilarity(a: number[], b: number[]): number {
  return 1 - cosineDistance(a, b);
}

// Text processing utilities
export function normalizeText(text: string): string {
  return text
    .toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '') // Remove diacritics
    .replace(/[^\w\s]/g, ' ') // Replace punctuation with spaces
    .replace(/\s+/g, ' ') // Normalize whitespace
    .trim();
}

export function extractKeywords(text: string, minLength: number = 3): string[] {
  const normalized = normalizeText(text);
  const words = normalized.split(' ');
  
  // Filter out common Italian stop words
  const stopWords = new Set([
    'il', 'lo', 'la', 'le', 'gli', 'una', 'un', 'dei', 'delle', 'del', 'dello', 'della',
    'di', 'da', 'in', 'con', 'su', 'per', 'tra', 'fra', 'a', 'ad', 'al', 'allo', 'alla',
    'ai', 'agli', 'alle', 'dal', 'dallo', 'dalla', 'dai', 'dagli', 'dalle', 'nel', 'nello',
    'nella', 'nei', 'negli', 'nelle', 'col', 'coi', 'sul', 'sullo', 'sulla', 'sui', 'sugli',
    'sulle', 'e', 'ed', 'o', 'od', 'ma', 'però', 'quindi', 'dunque', 'perciò', 'così',
    'che', 'chi', 'cui', 'quale', 'quali', 'quanto', 'quanta', 'quanti', 'quante',
    'questo', 'questa', 'questi', 'queste', 'quello', 'quella', 'quelli', 'quelle',
    'essere', 'avere', 'fare', 'dire', 'andare', 'venire', 'stare', 'dare', 'vedere'
  ]);

  return words
    .filter(word => word.length >= minLength && !stopWords.has(word))
    .filter((word, index, array) => array.indexOf(word) === index); // Remove duplicates
}

// Content chunking utilities
export function chunkText(text: string, chunkSize: number = 1000, overlap: number = 200): string[] {
  if (text.length <= chunkSize) {
    return [text];
  }

  const chunks: string[] = [];
  let start = 0;

  while (start < text.length) {
    let end = start + chunkSize;
    
    // Try to break at sentence boundaries
    if (end < text.length) {
      const sentenceEnd = text.lastIndexOf('.', end);
      const paragraphEnd = text.lastIndexOf('\n', end);
      
      if (sentenceEnd > start + chunkSize / 2) {
        end = sentenceEnd + 1;
      } else if (paragraphEnd > start + chunkSize / 2) {
        end = paragraphEnd + 1;
      }
    }

    chunks.push(text.slice(start, end).trim());
    start = Math.max(start + chunkSize - overlap, end - overlap);
  }

  return chunks;
}

// Agriculture-specific utilities
export function classifyAgriculturalTopic(text: string): string {
  const normalized = normalizeText(text);
  
  const topicKeywords = {
    'normative_pac': ['pac', 'politica agricola comune', 'regolamento', 'direttiva', 'normativa', 'legislazione'],
    'certificazioni': ['bio', 'biologico', 'certificazione', 'dop', 'igp', 'doc', 'docg', 'organico'],
    'tecnologie': ['iot', 'smart farming', 'tecnologia', 'sensori', 'droni', 'automazione', 'precision farming'],
    'finanziamenti': ['pnrr', 'bando', 'contributo', 'finanziamento', 'incentivo', 'fondo', 'agevolazione'],
    'coltivazione': ['semina', 'raccolto', 'irrigazione', 'fertilizzante', 'pesticida', 'coltura', 'pianta'],
    'meteo': ['meteo', 'clima', 'pioggia', 'temperatura', 'umidità', 'previsione', 'stagione'],
    'mercato': ['prezzo', 'vendita', 'commercio', 'export', 'import', 'mercato', 'economia'],
    'sostenibilità': ['sostenibile', 'ambientale', 'ecologico', 'verde', 'rinnovabile', 'carbon neutral']
  };

  let bestMatch = 'generale';
  let maxScore = 0;

  for (const [topic, keywords] of Object.entries(topicKeywords)) {
    const score = keywords.reduce((sum, keyword) => {
      return sum + (normalized.includes(keyword) ? 1 : 0);
    }, 0);

    if (score > maxScore) {
      maxScore = score;
      bestMatch = topic;
    }
  }

  return bestMatch;
}

// Validation utilities
export function validateUUID(uuid: string): boolean {
  const uuidRegex = /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i;
  return uuidRegex.test(uuid);
}

export function sanitizeInput(input: string): string {
  return input
    .replace(/<script\b[^<]*(?:(?!<\/script>)<[^<]*)*<\/script>/gi, '') // Remove script tags
    .replace(/<[^>]*>/g, '') // Remove HTML tags
    .replace(/javascript:/gi, '') // Remove javascript: protocol
    .replace(/on\w+\s*=/gi, '') // Remove event handlers
    .trim();
}

export function formatFileSize(bytes: number): string {
  if (bytes === 0) return '0 Bytes';
  
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
}

// Performance monitoring utilities
export function createPerformanceTimer() {
  const start = process.hrtime.bigint();
  
  return {
    elapsed: () => {
      const end = process.hrtime.bigint();
      return Number(end - start) / 1000000; // Convert to milliseconds
    },
    
    lap: () => {
      const now = process.hrtime.bigint();
      const elapsed = Number(now - start) / 1000000;
      return elapsed;
    }
  };
}

// Rate limiting utilities
export function createRateLimiter(maxRequests: number, windowMs: number) {
  const requests = new Map<string, number[]>();
  
  return {
    isAllowed: (identifier: string): boolean => {
      const now = Date.now();
      const userRequests = requests.get(identifier) || [];
      
      // Remove expired requests
      const validRequests = userRequests.filter(time => now - time < windowMs);
      
      if (validRequests.length >= maxRequests) {
        return false;
      }
      
      validRequests.push(now);
      requests.set(identifier, validRequests);
      return true;
    },
    
    reset: (identifier: string) => {
      requests.delete(identifier);
    },
    
    getRemainingRequests: (identifier: string): number => {
      const userRequests = requests.get(identifier) || [];
      const now = Date.now();
      const validRequests = userRequests.filter(time => now - time < windowMs);
      return Math.max(0, maxRequests - validRequests.length);
    }
  };
}
